.colorDiv {
    background-color: cornflowerblue;
    display: flex;
}
.listName, .listMessages {
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}
.dialogColumn{
flex-basis: 25%;
}
.listMessages{
flex-grow: 1;
}
.formArea{
    display: flex;
    flex-direction: column; 
    align-items: center;
    gap:5px;
    & textarea{
        width: 98%;
        font-size: 20px;
    }
    & button{
        font-size: 20px;
        margin-bottom: 10px;
    }
}