$fontSize:16px;

@mixin flexContainer($alignItems, $flexDirection, $justify) {
    display: flex;
    align-items: $alignItems;
    flex-direction: $flexDirection;
    justify-content: $justify;
}

.content{
    @include flexContainer(center,row,space-around );
    height: 100%;
    width: 95%;
    margin-top: 10px;
}
.avatar{
    @include flexContainer(center,column,center );
    position: relative;
   // flex-basis: 45%;
}
.changeAvatarInput{
    background-color: rgba($color: #9bcfe0, $alpha: 0.9);
    display:none;
    width: 100%;
    box-sizing: border-box;
    padding: 5px;
    position: absolute;
    bottom: 0;
  //  font-size: $fontSize;
}
.avatarImg:hover~.changeAvatarInput, .changeAvatarInput:hover, .avatarDefault:hover~.changeAvatarInput {
    display: inline-block;
}
.errorMessage{
    font-weight: 800;
    color: red;
}
.mainInfo{
    @include flexContainer(flex-start,column,center);
    flex-basis: 65%;
}
.formStatic{
    @include flexContainer(flex-start,column,center);
    flex-wrap: wrap;   
    width: 100%;
}
.fieldDescription{
    font-weight: 700;
    flex-basis: 25%;
}
.value{
    flex-basis: 70%;
}
.fieldContacts{
    @include flexContainer(flex-start,column,flex-start);
    width: 100%;
}
.contactsInstaticForm{
    @include flexContainer(flex-start,column,center);
    padding-left: 15px;
    margin: 10px 0;
    width: 100%;
}
.fieldFormStatic{
    @include flexContainer(center,row,flex-start);
    width: 100%;
    margin-bottom: 5px;
}
.fieldLink{
    color: black;
    text-decoration: none;
    transition:background-color 0.7s ease;
    box-sizing: content-box;
}
.fieldLink:hover{
    text-decoration: underline;
    background-color: #9bcfe0;
}
.changeButton{
    font-size: $fontSize;
    align-self: center;
    padding:5px 15px;
}
.iconColor{
   margin-right: 5px;
}
.firstcolumn{
    @include flexContainer(center,row,flex-start);
    flex-basis: 3%;
}
.dynamicForm{
    width: 100%;
}
.avatarDefault{
    background-color: #00a7c7;
    padding: 20px;
    border-radius: 50%;
}