$backColor:#e76f3d;
$hoverColor: #feab6b;

.navContainer{
    grid-area: n;
    background-color: $backColor;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
}
.link{
    padding-left: 10px;
    font-size: 1.5em;
    color: white;
    text-decoration: none;
}
.link:hover{
    background-color: $hoverColor;
}