$backColor:#9bcfe0;

@mixin flexContainer($flexDirection: column) {
  display: flex;
  flex-direction: $flexDirection;
  align-items: $alignItems;
}

.header {
  grid-area: h;
  background-color: $backColor;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.aboutLogin{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 20px;
  gap: 5px;
}
.loginInstriction{
  margin: 0;
  font-weight: 700;
  font-size: 20px;
}