.app-wrapper{
    display: grid; 
    grid-template-rows: 8% 1fr;
    grid-template-columns: 2fr 10fr;
    grid-template-areas: "h h" "n c"; 
    width: 100%;
}
.app-wrapper-content{
    grid-area: c;
}
p{
    margin: 0px;
}
button:hover {
    background-color:#00a7c7;;
}
button{
    transition:background-color 0.4s ease;
}