$backGroundColor: #feab6b;
@mixin flexContainer($alignItems, $flexDirection) {
    display: flex;
    align-items: $alignItems;
    flex-direction: $flexDirection;
}
.postContainer {
    background-color: $backGroundColor;
    @include flexContainer(center, column );
    margin-bottom: 10px;
    padding: 10px;
}
.post {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
    width: 100%;
}
.logo {
    width: 40px;
    height: 40px;
    padding: 5px;
    border-radius: 50%;
    background-color: #00a7c7;
    margin-right: 20px;
}
.aboutUser {
    @include flexContainer(center, column);
    flex-basis: 20%;
}
.aboutMessage {
    @include flexContainer(center, column);
}
.likeButton {
    @include flexContainer(center, row);
    justify-content: center;
    gap: 5px;
    padding: 5px;
    font-size: 20px;
    width: 100px;
}
.iLike.likeButton{
    background-color: red;
}