$backColor: #f3e9e7;

$fontSize: 24px;

@mixin flexContainer($flexDirection: column, $alignItems: flex-start) {
    display: flex;
    flex-direction: $flexDirection;
    align-items: $alignItems;
}
.loginContainer {
    background-color: $backColor;
    @include flexContainer(column, stretch);
    width: 100%;
    height: 100%;
}

.formButton {
    align-self: flex-start;
    font-size: $fontSize;
}
.formContainer {
    @include flexContainer(column, stretch);
    width: 70%;
    gap: 10px;
}
.fieldContainer {
    @include flexContainer(row, center);
    flex-grow: 1;
}
.container,
.formCenter,
.loginForm {
    @include flexContainer(column, center);
    width: 100%;
}
.formLabel {
    font-size: $fontSize;
}
.checkBoxStyle {
    transform: scale(1.4);
}
.errorMessage {
    color: red;
    font-size: $fontSize;
    font-weight: 700;
    margin-bottom: 10px;
}
.inputCaptcha{
    font-size: $fontSize;
    margin-left: 10px;
}
.additionalInfo{
    font-size: $fontSize;
}